<template>
    <div>
        shopList
        <!--        <el-table-->
        <!--                :data="tableData"-->
        <!--                style="width: 100%"  >-->
        <!--            <el-table-column-->
        <!--                    prop="date"-->
        <!--                    label=""-->
        <!--                    width="180" sortable>-->
        <!--            </el-table-column>-->
        <!--            <el-table-column-->
        <!--                    prop="name"-->
        <!--                    label=""-->
        <!--                    width="180">-->
        <!--            </el-table-column>-->
        <!--            <el-table-column-->
        <!--                    prop="address"-->
        <!--                    label="">-->
        <!--            </el-table-column>-->
        <!--            <el-table-column-->
        <!--                    prop="age"-->
        <!--                    label="age"-->
        <!--                    :filters="[{text:22,value:22},{text:33,value:33}]"-->
        <!--                    :filter-method=" filtersMethod" :formatter="formatter">-->
        <!--            </el-table-column>-->
        <!--        </el-table>-->
        <el-table
                :data="list"
                style="width: 100%" @filter-change="onFilterChange">
            <el-table-column
                    prop="name"
                    label=""
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="age"
                    label=""
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="sex"
                    label="">
            </el-table-column>
            <el-table-column
                    prop="statusName"
                    label=""
                    :filters="filters"
                    column-key="status"
                    :filter-method="tst"
            >
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
  export default {
    name: "shopList",
    data() {
      return {
        tableData: [
          {
            date: '2016-05-02',
            name: '',
            address: ' 1518 ',
            age: 22, sex: 22
          },
          {
            date: '2016-05-01',
            name: '',
            address: ' 1517 ',
            age: 33
          }, {
            date: '2016-05-04',
            name: '',
            address: ' 1517 ',
            age: 33
          }, {
            date: '2016-05-06',
            name: '',
            address: ' 1517 ',
            age: 18
          }, {
            date: '2016-05-04',
            name: '',
            address: ' 1517 ',
            age: 22,
            sex: 66
          }],
        list: [],
        status: [],
      }
    },
    // asyncComputed: {
    //   async filters() {
    //     const res = await this.aa()
    //     return res
    //   }
    // },
    // computed:{
    //   filters(){
    //     return [
    //       {text: "", value: "show"},
    //       {text: "", value: "hide"}
    //     ]
    //   }
    // },
    methods: {
      formatter(row) {
        return row.age === 22 ? ',' : ''
      },
      tst() {
         return true
      },
      aa() {
        return new Promise(resolve => {
          const filters = [
            {text: "", value: "show"},
            {text: "", value: "hide"}
          ]
            resolve(filters)
        })
      },
      getList() {
        this.$request.get("/list")
          .then(res => {
            let list = res.data.data
            this.list = [...list]
          })
      },
      onFilterChange(filter) {
        this.status = filter.status
      }
    },
    async created() {
        this.filters=await  this.aa()
      this.getList()
    },
    watch: {
      status(newValue) {
        const status = newValue.join(',')
        this.$request.get("/params", {
          params: {status}
        })
          .then(res => {
            const list = res.data.data
            this.list = [...list]

          })
      }
    }
  }
</script>

